<template>
   <form  @submit.prevent="onSubmit">
      <v-text-field
         v-validate="'required|email'"
         v-model="user.email"
         :error-messages="errors.collect('email')"
         label="Email"
         data-vv-name="email"
         required
      />

      <v-text-field
         ref="password"
         type="password"
         v-validate="'required|min:6|max:12'"
         v-model="user.password"
         :counter="12"
         :error-messages="errors.collect('password')"
         label="密碼"
         data-vv-name="password"
         required
      />

      <v-text-field
         type="password"
         v-validate="'required|confirmed:password'"
         v-model="user.confirmPassword"
         :counter="12"
         :error-messages="errors.collect('confirmPassword')"
         label="確認密碼"
         data-vv-name="confirmPassword"
         required
      />

      <v-text-field
         v-validate="'required|phone'"
         v-model="user.phone"
         :error-messages="errors.collect('phone')"
         label="手機"
         data-vv-name="phone"
         required
      />

      <v-text-field
         v-validate="'required'"
         v-model="user.fullname"
         :error-messages="errors.collect('fullname')"
         label="姓名"
         data-vv-name="fullname"
         required
      />

      <v-radio-group v-model="user.gender" row>
         <v-radio v-for="(item, index) in genderOptions" :key="index" 
            :label="item.text" :value="item.value" 
         />
      </v-radio-group>
      
      <v-btn @click.prevent="onSubmit" color="success" class="cn">註冊</v-btn>
   </form>
</template>



<script>

export default {
   name: 'RegisterForm',
   data() {
      return {
         user: {
            email: '',
            password: '',
            confirmPassword: '',
            phone: '',
            fullname: '',
            gender: 1            
         },

         genderOptions: Helper.genderOptions()
         
      }
   },
   methods: {
      onSubmit() {
         this.$validator.validate().then(valid => {
            if(valid) this.$emit('submit', this.user);
         });         
      }
   }
};
</script>
